<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container webconnection">
                    <div class="modal-header">
                        <div class="head3">Tron web wallet not found!</div>
                    </div>
                    <div class="modal-body">
                        <div class="head3">Please run RiddleHASH game with Tronlink or compatible tron wallet</div>
                    </div>
                    <div class="modal-footer">
                        <slot name="footer">
                            <button class="modal-default-button" @click="reloadPage()">Reload Game</button>
                        </slot>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapState } from 'vuex';
    export default {
        computed:
            mapState({
                tronWebConnected: state => state.tronWeb.connected
            }),
        mounted(){
            if (this.tronWebConnected) {
                this.reloadPage()
            }
        },
        methods: {
            reloadPage(){
                window.location.reload()
            }
        }
    }
</script>